<template>
    <ul class="emp-list">
        <li v-for="emp in data" :class="{ expanded: emp.expanded }" class="emp">
            <div class="emp-summary" @click="onEmpClick(emp)">
                <span class="emp-name"
                    >{{ emp.EmployeeName || emp.Name }}
                    <span v-if="infoConfig['EmployeeNo']"
                        >({{ emp.EmployeeNo }})</span
                    >
                </span>
                <span
                    :class="{
                        'icon-arrow-line-big-right': !emp.expanded,
                        'icon-arrow-line-big-left': emp.expanded,
                    }"
                    class="expand-icon h-icon-expand"
                ></span>
                <span
                    :class="{
                        'icon-arrow-line-big-down': !emp.expanded,
                        'icon-arrow-line-big-up': emp.expanded,
                    }"
                    class="expand-icon v-icon-expand"
                ></span>
            </div>
            <emp-info
                :config="infoConfig"
                :data="emp"
                :level="level"
                :max-level="maxLevel"
            ></emp-info>
        </li>
    </ul>
</template>

<script>
import cache from "./cache";
// import EmpInfo from "./EmpInfo";
import Remote from "../../remote";

export default {
    name: "emp-list",
    // components: {
    //     EmpInfo
    // },
    props: {
        data: Array,
        level: Number,
        maxLevel: Number,
        selectionMode: Boolean,
        infoConfig: Array,
    },
    methods: {
        onEmpClick(emp) {
            this.$set(emp, "expanded", !emp.expanded);

            let data = this.data || [];
            let cachedEmp = cache[emp.EmployeeNo];

            if (cachedEmp) {
                let index = data.indexOf(emp);
                data.splice(index, 1, cachedEmp);
                cachedEmp.expanded = emp.expanded;
                emp = cachedEmp;
            }

            data.forEach((e) => {
                if (this.selectionMode && e != emp) {
                    this.$set(e, "expanded", false);
                }
            });

            if (emp.expanded) {
                if (!cachedEmp) {
                    cache[emp.EmployeeNo] = emp;
                    this.$set(emp, "$loading", true);

                    this.$ajax
                        .post(Remote.getDetails, `employeeNo=${emp.EmployeeNo}`)
                        .then((resp) => {
                            this.$set(emp, "Manager", resp.data.Manager);
                            this.$set(
                                emp,
                                "TeamMembers",
                                resp.data.TeamMembers
                            );
                        })
                        .catch(() => {
                            delete cache[emp.EmployeeNo];
                        })
                        .then(() => {
                            emp.$loading = false;
                        });
                }
            }

            if (this.selectionMode) {
                this.$emit("selection-change", emp.expanded ? emp : null);
            }
        },
    },
};
</script>

<style lang="scss">
.emp-list {
    .emp {
        position: relative;
        padding-top: 2px;
        padding-left: 2px;

        &.expanded {
            & > .emp-info {
                display: block;
            }
        }

        .emp-summary {
            background-color: #f3f3f3;
            position: relative;
            padding: 0 10px;
            cursor: pointer;

            .emp-name {
                line-height: 35px;
            }

            .expand-icon {
                font-size: 20px;
                line-height: 35px;
                position: absolute;
                top: 0;
                right: 10px;
            }

            .h-icon-expand {
                display: none;
            }
        }

        .emp-info {
            display: none;
        }
    }
}
</style>
