<template>
    <tile
        :loading="loading"
        class="w-100 secret-questions"
        icon="icon-help-round"
        title="Secret Questions"
    >
        <template v-for="(a, i) in answers">
            <tile :title="'Question ' + (i + 1)" class="w-100 question">
                <div class="section">
                    <div class="section-body">
                        <div class="default-form">
                            <div class="form-row form-control">
                                <select
                                    :value="a.Id"
                                    @change="handleSelect($event, a, i)"
                                >
                                    <option v-for="q of options" :value="q.Id">
                                        {{ q.Text }}
                                    </option>
                                </select>
                            </div>
                            <div class="form-row form-control">
                                <label>Answer:</label>
                                <input v-model="a.AnswerText" type="text" />
                            </div>
                        </div>
                    </div>
                </div>
            </tile>
        </template>
        <div class="section">
            <div class="section-body">
                <div class="default-form">
                    <div
                        v-if="submitting"
                        class="form-row row-bottom loader-box"
                    ></div>
                    <div v-else class="form-row row-bottom">
                        <button class="button green" @click="save()">
                            <span class="b-icon icon-save"></span>
                            <span>Save</span>
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </tile>
</template>

<style scoped>
.newui .section .default-form .form-control {
    max-width: 420px;
    margin: 0 0 20px;
}

.newui .section .default-form .form-row select,
.newui .section .default-form .form-control > label {
    display: block;
    width: 100%;
    max-width: 420px;
}

.newui .section .default-form .form-control > label {
    padding: 0;
    margin-bottom: 4px;
}
body.newui .dashboard .tilebody {
    padding: 0 20px 20px 12px;
}
body.newui .dashboard .pagecontent .section .section-body {
    padding-top: 0;
}
</style>

<script>
import { mapActions } from "vuex";

export default {
    data() {
        return {
            loading: true,
            options: {},
            answers: [{}, {}, {}],
            submitting: false,
        };
    },
    methods: {
        ...mapActions(["addToast", "showRequestError"]),
        save() {
            this.loading = true;
            this.$ajax
                .post("/api/Security/SaveAnswers", this.answers)
                .then((resp) => {
                    if (resp.data.Success) {
                        this.addToast({
                            title: "Questions are saved",
                            message: "Questions have been successfully saved.",
                            type: "success",
                        });
                    } else {
                        this.addToast({
                            title: "Couldn't update questions",
                            message: resp.data.Error,
                            type: "error",
                        });
                    }
                })
                .catch()
                .then(() => {
                    this.loading = false;
                });
        },

        handleSelect(e, answer, index) {
            let question = { ...this.options[Number(e.target.value) - 1] };
            question.AnswerId = answer.AnswerId;
            question.AnswerText = answer.AnswerText;
            this.$set(this.answers, index, question);
        },
    },

    mounted() {
        this.loading = true;
        this.$ajax
            .post("/api/Security/GetAnswers")
            .then((resp) => {
                this.options = resp.data.Questions;
                this.answers = [];

                let i = 0;
                this.options.forEach((q) => {
                    if (q.AnswerId && i < 3) {
                        this.$set(this.answers, i++, { ...q });
                    }
                });

                this.answers.sort((a, b) => a.AnswerId - b.AnswerId);
            })
            .then(() => {
                this.loading = false;
            });
    },
};
</script>
