<template>
    <tile
        class="change-password w-100"
        icon="icon-lock"
        title="Change Password"
    >
        <div class="section">
            <div class="section-body">
                <ul
                    class="password-guide-list pwd-error"
                    v-show="outFocusMessage !== '' && !showPwdGuide"
                >
                    <li>
                        <span class="password-ctrl-icon validation-ico"></span
                        >{{ outFocusMessage }}
                    </li>
                </ul>
                <div class="default-form">
                    <div class="form-row form-control">
                        <label>Current Password:</label>
                        <div class="password-ctrl-input pwd">
                            <input
                                v-model="current"
                                v-bind:type="showCurrent ? 'text' : 'password'"
                                autocomplete="off"
                            />
                            <span
                                class="eye-toggle"
                                @mousedown="showCurrent = true"
                                @mouseleave="showCurrent = false"
                                @mouseup="showCurrent = false"
                            >
                                <app-icons
                                    asset="eye-invisible"
                                    v-if="!showCurrent"
                                />
                                <app-icons asset="eye" v-if="showCurrent" />
                            </span>
                        </div>
                    </div>
                    <div class="form-row form-control">
                        <label>New Password:</label>
                        <div class="password-ctrl-input pwd">
                            <input
                                v-model="next"
                                @focus="handlePwdFocus"
                                @blur="handlePwdBlur"
                                @input="validatePassword"
                                v-bind:type="showNext ? 'text' : 'password'"
                                autocomplete="off"
                            />
                            <span
                                class="eye-toggle"
                                :class="{
                                    'valid-pwd': passed == true,
                                    'invalid-pwd': passed == false && isMFAV2,
                                }"
                                @mousedown="showNext = true"
                                @mouseleave="showNext = false"
                                @mouseup="showNext = false"
                            >
                                <app-icons
                                    asset="eye-invisible"
                                    v-if="!showNext"
                                />
                                <app-icons asset="eye" v-if="showNext" />
                            </span>
                            <div
                                class="password-ctrl-guide"
                                v-show="showPwdGuide"
                            >
                                <div>
                                    <ul class="password-guide-list">
                                        <li
                                            :class="{
                                                'validation--passed': hasLength,
                                            }"
                                        >
                                            <span
                                                class="password-ctrl-icon validation-ico"
                                                ><app-icons
                                                    asset="close-circle"
                                                    v-if="
                                                        !hasLength
                                                    " /><app-icons
                                                    asset="check-circle"
                                                    v-if="hasLength"
                                            /></span>
                                            Must contain at least 8 characters.
                                        </li>
                                        <li
                                            :class="{
                                                'validation--passed':
                                                    hasAlphaChars,
                                            }"
                                        >
                                            <span
                                                class="password-ctrl-icon validation-ico"
                                                ><app-icons
                                                    asset="close-circle"
                                                    v-if="
                                                        !hasAlphaChars
                                                    " /><app-icons
                                                    asset="check-circle"
                                                    v-if="hasAlphaChars"
                                            /></span>
                                            Must contain at least one UPPERCASE
                                            and one lowercase character.
                                        </li>
                                        <li
                                            :class="{
                                                'validation--passed':
                                                    hasNumber && hasSpecial,
                                            }"
                                        >
                                            <span
                                                class="password-ctrl-icon validation-ico"
                                                ><app-icons
                                                    asset="close-circle"
                                                    v-if="
                                                        !(
                                                            hasNumber &&
                                                            hasSpecial
                                                        )
                                                    " /><app-icons
                                                    asset="check-circle"
                                                    v-if="
                                                        hasNumber && hasSpecial
                                                    "
                                            /></span>
                                            Must contain at least one number or
                                            special character (@#$%&*!).
                                        </li>
                                        <li
                                            :class="{
                                                'validation--passed':
                                                    noIdenticalChars,
                                            }"
                                        >
                                            <span
                                                class="password-ctrl-icon validation-ico"
                                                ><app-icons
                                                    asset="close-circle"
                                                    v-if="
                                                        !noIdenticalChars
                                                    " /><app-icons
                                                    asset="check-circle"
                                                    v-if="
                                                        noIdenticalChars
                                                    " /></span
                                            >Must not contain more than two
                                            identical characters in a row (aaa,
                                            444).
                                        </li>
                                        <li
                                            :class="{
                                                'validation--passed':
                                                    noIncrementingNumbers,
                                            }"
                                        >
                                            <span
                                                class="password-ctrl-icon validation-ico"
                                                ><app-icons
                                                    asset="close-circle"
                                                    v-if="
                                                        !noIncrementingNumbers
                                                    " /><app-icons
                                                    asset="check-circle"
                                                    v-if="
                                                        noIncrementingNumbers
                                                    " /></span
                                            >Must not contain more than two
                                            incrementing numbers (12 is OK but
                                            123 is not).
                                        </li>

                                        <li
                                            :class="{
                                                'validation--passed':
                                                    noReservedWords,
                                            }"
                                        >
                                            <span
                                                class="password-ctrl-icon validation-ico"
                                                ><app-icons
                                                    asset="close-circle"
                                                    v-if="
                                                        !noReservedWords
                                                    " /><app-icons
                                                    asset="check-circle"
                                                    v-if="
                                                        noReservedWords
                                                    " /></span
                                            >Must not contain your given names,
                                            username, email address, or the
                                            words ‘affinity’ or 'pass’.
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="form-row form-control">
                        <label>Confirm Password:</label>
                        <div class="password-ctrl-input repwd">
                            <input
                                v-model="confirm"
                                v-bind:type="showRePwd ? 'text' : 'password'"
                                @input="checkRetypedPassword"
                                autocomplete="off"
                            />
                            <span
                                class="eye-toggle"
                                :class="{
                                    'valid-pwd': hasConfirmed == true,
                                    'invalid-pwd': hasConfirmed == false,
                                }"
                                @mousedown="showRePwd = true"
                                @mouseleave="showRePwd = false"
                                @mouseup="showRePwd = false"
                            >
                                <app-icons
                                    asset="eye-invisible"
                                    v-if="!showRePwd"
                                />
                                <app-icons asset="eye" v-if="showRePwd" />
                            </span>
                        </div>
                    </div>
                    <div
                        v-if="submitting"
                        class="form-row row-bottom loader-box"
                    ></div>
                    <div v-else class="form-row form-control">
                        <button class="button green" @click="save()">
                            <span class="b-icon icon-save"></span
                            ><span>Save</span>
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </tile>
</template>

<style scoped>
.newui .section .default-form .form-control {
    max-width: 420px;
    margin: 0 0 20px;
}

.newui .section .default-form .form-control input[type="password"],
.newui .section .default-form .form-control input[type="text"],
.newui .section .default-form .form-control > label {
    display: block;
    width: 100%;
    max-width: 420px;
}

.newui .section .default-form .form-control input[type="password"] {
    padding-right: 32px;
}

.newui .section .default-form .form-control > label {
    padding: 0;
    margin-bottom: 4px;
}
.password-ctrl-input {
    position: relative;
    z-index: 8;
}

.password-ctrl-input.repwd {
    z-index: 7;
}

.password-ctrl-input.pwd > input {
    position: relative;
    z-index: 11;
}

.password-ctrl-guide {
    position: absolute;
    top: calc(100% - 8px);
    left: 0;
    right: 0;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    min-height: 60px;
    overflow: hidden;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
    background-color: #fff;
    z-index: 10;
    margin-left: -12px;
    margin-right: -12px;
}

.password-ctrl-guide > div {
    padding: 12px 20px 8px;
}

ul.password-guide-list {
    margin: 0;
    padding-left: 0;
    list-style: none;
}

ul.password-guide-list > li {
    font-family: Arial, Helvetica, sans-serif;
    padding-left: 20px;
    position: relative;
    line-height: 1.5;
}

span.password-ctrl-icon {
    position: absolute;
    width: 14px;
    height: 14px;
}

span.password-ctrl-icon .app-icon {
    font-size: 14px;
    height: 14px;
}

span.eye-toggle {
    position: absolute;
    right: 8px;
    top: 10px;
    font-size: 16px;
    cursor: pointer;
    z-index: 11;
}

span.eye-toggle.valid-pwd {
    color: #4caf50;
}

span.eye-toggle.invalid-pwd {
    color: #f44336;
}

ul.password-guide-list > li span.validation-ico {
    left: 0;
    top: 2px;
}

ul.password-guide-list > li.validation--passed {
    color: #bababa;
}

ul.password-guide-list > li .app-icon {
    color: #eb0029;
}

ul.password-guide-list > li.validation--passed .app-icon {
    color: #0fa140;
}

.password-ctrl-guide ul li {
    line-height: 1.75;
    margin-bottom: 4px;
}

ul.pwd-error {
    position: relative;
    margin-top: -24px;
    background-color: #fff4f4;
    border-radius: 10px;
    padding: 8px 12px;
    margin-bottom: 8px;
    max-width: 400px;
}

ul.pwd-error li {
    font-size: 10px;
}
ul.pwd-error > li span.validation-ico {
    top: 0;
}

.newui button.blue:disabled {
    background: #f4f4f4;
    color: #bbbbbb;
    cursor: not-allowed;
}
</style>

<script>
import { mapActions } from "vuex";
import AppIcons from "../../AppIcons.vue";

let timer = null;
let timerRetypePwd = null;
export default {
    setup() {
        return {
            isMFAV2: Affinity.isMonsoon,
        };
    },
    comments: {
        AppIcons,
    },
    data() {
        return {
            submitting: false,
            current: "",
            next: "",
            confirm: "",
            error: "",
            passed: undefined,
            hasLength: false,
            hasNumber: false,
            hasAlphaChars: false,
            hasSpecial: false,
            noIncrementingNumbers: false,
            noIdenticalChars: false,
            noReservedWords: false,
            hasConfirmed: undefined,

            showPwdGuide: false,
            showRpwdGuide: false,
            showCurrent: false,
            showNext: false,
            showRePwd: false,
            outFocusMessage: "",
            cancelValidatePassword: undefined,
        };
    },
    methods: {
        ...mapActions(["addToast", "showRequestError"]),
        save() {
            if (this.validate()) {
                this.submitting = true;

                this.$ajax
                    .post("/api/Security/UpdatePassword", {
                        CurrentPassword: this.isMFAV2
                            ? btoa(this.current)
                            : this.current,
                        NewPassword: this.isMFAV2 ? btoa(this.next) : this.next,
                    })
                    .then((resp) => {
                        const { Result, Success, Error, ValidationErrors } =
                            resp.data;
                        const isSuccess = !this.isMFAV2
                            ? Success
                            : Result === "SUCCESS";
                        this.error = "";
                        if (isSuccess) {
                            this.current = "";
                            this.next = "";
                            this.confirm = "";
                            this.hasConfirmed = undefined;
                            this.passed = false;
                            this.addToast({
                                type: "success",
                                title: "Password is changed",
                                message:
                                    "Password has been successfully changed.",
                            });
                        } else {
                            let message = "";
                            if (this.isMFAV2) {
                                const keys = Object.keys(ValidationErrors);
                                keys.forEach((key) => {
                                    if (
                                        (message =
                                            ValidationErrors[key].Message)
                                    ) {
                                        return false;
                                    }
                                });
                            } else {
                                message = Error;
                            }
                            this.addToast({
                                type: "error",
                                message,
                                title: "Couldn't change the password",
                            });
                        }
                    })
                    .catch()
                    .then(() => {
                        this.submitting = false;
                    });
            }
        },
        validate() {
            let error = null;

            if (!this.current) error = "Current password is required.";
            else if (!this.next) error = "New password is not entered.";
            else if (!this.confirm || this.next != this.confirm) {
                error = `Password is not confirmed${
                    this.confirm ? " correctly" : ""
                }.`;
            } else if (!this.passed) {
                error = "Password does not meet the requirements.";
            }

            if (error)
                this.addToast({
                    type: "error",
                    title: "Password validation failed",
                    message: error,
                });

            return !error;
        },
        handlePwdFocus() {
            this.showPwdGuide = this.isMFAV2 ? true : false;
        },
        handlePwdBlur() {
            if (this.isMFAV2) {
                this.showPwdGuide = false;
            }
        },
        checkForIncrementingNumbers(strArr) {
            if (this.next && strArr.length >= 4) {
                const nums = "0123456789";
                this.noIncrementingNumbers = true;
                // Convert the password string to an array of characters
                strArr.forEach((_, index) => {
                    const partialStr = strArr.slice(index, index + 4).join("");
                    // Check if the next character is the next number in the sequence
                    if (
                        nums.includes(partialStr) &&
                        index + 2 < strArr.length
                    ) {
                        this.noIncrementingNumbers = false;
                    }
                });
            }
        },
        checkForSameCharacters(strArr) {
            if (this.next && strArr.length >= 3) {
                this.noIdenticalChars = true;
                let count = 0,
                    mainChar = "";
                strArr.forEach((char) => {
                    if (char === mainChar) {
                        count++;
                    } else {
                        count = 1;
                        mainChar = char;
                    }

                    if (count > 2) {
                        this.noIdenticalChars = false;
                    }
                });
            }
        },
        async apiPasswordValidation() {
            const { next } = this;
            if (next && next.length >= 4 && this.isMFAV2) {
                this.$ajax
                    .post("/Security/ValidatePassword", {
                        password: btoa(next),
                    })
                    .then((res) => {
                        const { Result, ValidationErrors } = res.response.data;
                        const keys = Object.keys(ValidationErrors);
                        let message = "";
                        keys.forEach((key) => {
                            if ((message = ValidationErrors[key].Message)) {
                                return false;
                            }
                        });
                        this.outFocusMessage =
                            Result === "FAILED" ? message : "";
                        this.noReservedWords =
                            ValidationErrors.NoInvalidWords.Success;
                    });
            }
        },
        async validatePassword() {
            const password = this.next;
            if (timer) {
                clearTimeout(timer);
                timer = null;
            }
            if (password) {
                timer = setTimeout(async () => {
                    this.outFocusMessage = "";
                    if (this.isMFAV2) {
                        this.hasLength = password.length >= 8;
                        this.hasNumber = /\d/.test(password);
                        this.hasAlphaChars =
                            /[a-z]/.test(password) && /[A-Z]/.test(password);
                        this.hasSpecial = /[!@#\$%\^\&*\)\(+=._-]/.test(
                            password
                        );

                        const strArr = password.match(/.{1,1}/g);

                        this.checkForIncrementingNumbers(strArr);
                        this.checkForSameCharacters(strArr);
                        await this.apiPasswordValidation();
                        this.passed = ![
                            this.hasAlphaChars,
                            this.hasLength,
                            this.hasNumber,
                            this.hasSpecial,
                            this.noIdenticalChars,
                            this.noIncrementingNumbers,
                            this.noReservedWords,
                        ].includes(false);
                        if (this.passed) {
                            this.showPwdGuide = false;
                        } else this.showPwdGuide = true;
                    } else this.passed = true;

                    // Run the validation only if password is at least 4 characters long
                }, 500);
            } else {
                this.passed = undefined;
                this.hasLength = false;
                this.hasNumber = false;
                this.hasAlphaChars = false;
                this.hasSpecial = false;
                this.noIdenticalChars = false;
                this.noIncrementingNumbers = false;
                this.noReservedWords = true;
                this.confirm = "";
                this.hasConfirmed = undefined;
            }
        },
        checkRetypedPassword() {
            const { next, confirm } = this;
            if (!next || !confirm) {
                if (!confirm) this.hasConfirmed = undefined;
                return false;
            }
            if (timerRetypePwd) {
                clearTimeout(timerRetypePwd);
                timerRetypePwd = null;
            }

            timerRetypePwd = setTimeout(() => {
                this.hasConfirmed = next === confirm;
                if (this.hasConfirmed) {
                    this.outFocusMessage = "";
                } else {
                    this.outFocusMessage = "Password fields do not match.";
                }
            }, 250);
        },
    },
};
</script>
