<template>
    <div class="security-manager tile-group">
        <password></password>
        <questions v-if="isOldAuthUser" v-cloak></questions>
    </div>
</template>

<script>
import Password from "./password.vue";
import Questions from "./questions.vue";
import Emails from "./emails.vue";

export default {
    data() {
        return {
            isOldAuthUser: undefined,
            questions: {},
        };
    },
    components: {
        Password,
        Questions,
        Emails,
    },
    mounted() {
        // Check if the user is an old auth user
        this.isOldAuthUser = !Affinity.isMonsoon;
    },
};
</script>
