import "../../dashboard-common/css/common/ui.default.css";

import App from "./App.vue";
import router from "./router";
import Vue from "vue";
import store from "./store";
import remote from "./remote";

import Draggable from "vuedraggable";
import Grid from "../../dashboard-common/vue/grid.vue";
import Modal from "../../dashboard-common/vue/modal.vue";
import Toaster from "../../dashboard-common/vue/toaster.vue";
import Tile from "../../dashboard-common/vue/dashboard/tile.vue";
import UploadList from "../../dashboard-common/vue/upload/list.vue";
import Datepicker from "../../dashboard-common/vue/datepicker.vue";

import Switch from "../../dashboard-common/vue/switch.vue";

import EmpInfo from "./views/search/EmpInfo";
import EmpList from "./views/search/EmpList";

import AppIcons from "./views/AppIcons.vue";

import "./styles/dashboard.default.less";
import "./styles/index.scss";

import axios from "axios";

import Ajax from "./remote/ajax";
Vue.use(Ajax, {
    handleUnauthorized: true,
});

Vue.component("grid", Grid);
Vue.component("modal", Modal);
Vue.component("tile", Tile);
Vue.component("toaster", Toaster);
Vue.component("uploadlist", UploadList);
Vue.component("draggable", Draggable);
Vue.component("datepicker", Datepicker);
Vue.component("x-switch", Switch);

Vue.component("emp-info", EmpInfo);
Vue.component("emp-list", EmpList);

Vue.component("app-icons", AppIcons);

Vue.config.productionTip = false;

const config = Vue.observable({ config: null });

Object.defineProperty(Vue.prototype, "$config", {
    get() {
        return config.config;
    },

    set(value) {
        config.config = value;
    },
});

Vue.prototype.$checkMFACookie = async function (page) {
    const res = await Vue.prototype.$ajax.post(
        `${process.env.VUE_APP_MFA_API}/api/Auth?page=${page}`,
        null,
        {
            withCredentials: true,
        }
    );
    const { authenticated, mfaRequired, otpChannels } = res.data;
    if (!authenticated && mfaRequired) {
        window.location.href = `${process.env.VUE_APP_MFA_URL}?redirectUrl=${
            window.location.href
        }&channel=${JSON.stringify(otpChannels)}&page=${page}`;
    }
};

const user = Vue.observable({ user: null });

Object.defineProperty(Vue.prototype, "$user", {
    get() {
        return user.user;
    },

    set(value) {
        user.user = value;
    },
});

window.myVue = new Vue({
    router,
    render: (h) => h(App),
    // el: '#app',
    store,
    // components: {
    //     EmpInfo,
    //     EmpList
    // }
}).$mount("#app");
